.gallery-viewer {
    position: relative;
    padding: 20px;
    text-align: center;
}

.close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 2rem;
    cursor: pointer;
}

.viewer-content {
    max-width: 800px;
    margin: 0 auto;
}

.media-container {
    position: relative;
    margin-top: 20px;
}

.media-content {
    width: 100%;
    max-height: 450px;
    overflow: hidden;
}

.media-content img,
.media-content iframe,
.media-content audio {
    width: 100%;
    height: auto;
}

.media-content video {
    max-height: 450px;
}

.nav-button {
    position: absolute;
    top: 50%;
    background-color: rgba(0,0,0,0.5);
    border: none;
    color: #fff;
    padding: 10px;
    font-size: 2rem;
    cursor: pointer;
    transform: translateY(-50%);
    z-index: 10;
}

.prev-button {
    left: 0;
}

.next-button {
    right: 0;
}

.nav-button:hover {
    background-color: rgba(0,0,0,0.8);
}
